[hidden] {
    display: none !important;
}

.react-fine-uploader-gallery-nodrop-container,
.react-fine-uploader-gallery-dropzone {
    border-radius: 6px;
    background-color: #FAFAFA;
    max-height: 490px;
    min-height: 310px;
    overflow-y: hidden;
    padding: 15px 15px 15px 5px;
    position: relative;
}

.react-fine-uploader-gallery-dropzone {
    border: 2px dashed #00ABC7;
}
.react-fine-uploader-gallery-dropzone-upload-icon {
    height: 36px;
    margin-bottom: -6px;
    margin-right: 10px;
    width: 36px;
}

.react-fine-uploader-gallery-nodrop-container {
    border: 2px solid #00ABC7;
}

.react-fine-uploader-gallery-dropzone-active  {
    background: #FDFDFD;
    border: 2px solid #00ABC7;
}

.react-fine-uploader-gallery-dropzone-content,
.react-fine-uploader-gallery-nodrop-content {
    font-size: 36px;
    left: 0;
    opacity: 0.25;
    position: absolute;
    text-align: center;
    top: 38%;
    width: 100%;
}

.react-fine-uploader-gallery-file-input-container {
    background: #00ABC7;
    border: 1px solid #37B7CC;
    border-radius: 3px;
    color: #FFFFFF;
    display: inline;
    float: left;
    margin-left: 10px;
    padding-bottom: 7px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 7px;
    text-align: center;
    width: 105px;
}
.react-fine-uploader-gallery-file-input-container:hover {
    background: #33B6CC;
}
.react-fine-uploader-gallery-file-input-container:focus {
    outline: 1px dotted #000000;
}
.react-fine-uploader-gallery-file-input-content {
    display: inline-block;
    margin-top: -2px;
}
.react-fine-uploader-gallery-file-input-upload-icon {
    fill: white;
    height: 24px;
    margin-bottom: -6px;
    margin-right: 5px;
    width: 24px;
}

.react-fine-uploader-gallery-progress-bar,
.react-fine-uploader-gallery-total-progress-bar {
    border-radius: 3px;
}
.react-fine-uploader-gallery-progress-bar-container,
.react-fine-uploader-gallery-total-progress-bar-container {
    background: #F2F2F2;
    border-radius: 3px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2) inset;
    position: absolute;
}
.react-fine-uploader-gallery-total-progress-bar-container {
    display: inline-block;
    height: 25px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 4px;
    width: 70%;
}
.react-fine-uploader-gallery-progress-bar,
.react-fine-uploader-gallery-total-progress-bar {
    background: #00ABC7;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2) inset;
    height: inherit;
}
.react-fine-uploader-gallery-progress-bar-container {
    height: 15px;
    left: 50%;
    opacity: 0.9;
    top: 60px;
    transform: translateX(-50%);
    width: 90%;
    z-index: 1;
}

.react-fine-uploader-gallery-files {
    clear: both;
    list-style: none;
    max-height: 450px;
    overflow-y: auto;
    padding-left: 0;
    padding-top: 15px;
}
.react-fine-uploader-gallery-files-enter {
    opacity: 0.01;
}
.react-fine-uploader-gallery-files-enter.react-fine-uploader-gallery-files-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}
.react-fine-uploader-gallery-files-exit {
    opacity: 1;
}
.react-fine-uploader-gallery-files-exit.react-fine-uploader-gallery-files-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}

.react-fine-uploader-gallery-file {
    background-color: #FFFFFF;
    border-radius: 9px;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.22);
    display: inline-block;
    font-size: 13px;
    height: 165px;
    line-height: 16px;
    margin: 0 25px 25px 10px;
    position: relative;
    vertical-align: top;
    width: 130px;
}

.react-fine-uploader-gallery-thumbnail-container {
    display: block;
    height: 130px;
    text-align: center;
}
.react-fine-uploader-gallery-thumbnail {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.react-fine-uploader-gallery-thumbnail-icon-backdrop,
.react-fine-uploader-gallery-upload-failed-icon,
.react-fine-uploader-gallery-upload-success-icon {
    left: 50%;
    opacity: 0.5;
    position: absolute;
    top: 39%;
    transform: translate(-50%, -50%);
}
.react-fine-uploader-gallery-upload-failed-icon,
.react-fine-uploader-gallery-upload-success-icon {
    height: 60px;
    width: 60px;
    z-index: 1;
}
.react-fine-uploader-gallery-upload-success-icon {
    fill: green;
}
.react-fine-uploader-gallery-upload-failed-icon {
    fill: red;
}
.react-fine-uploader-gallery-thumbnail-icon-backdrop {
    background-color: white;
    border-radius: 30px;
    height: 50px;
    width: 50px;
}

.react-fine-uploader-gallery-file-footer {
    padding-left: 5px;
    padding-right: 5px;
}

.react-fine-uploader-gallery-filename {
    display: block;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.react-fine-uploader-gallery-filesize {
    display: block;
    float: right;
}

.react-fine-uploader-gallery-status {
    font-style: italic;
}

.react-fine-uploader-gallery-cancel-button:hover svg,
.react-fine-uploader-gallery-delete-button:hover svg,
.react-fine-uploader-gallery-pause-resume-button:hover svg,
.react-fine-uploader-gallery-retry-button:hover svg {
    fill: grey;
}
.react-fine-uploader-gallery-cancel-button:focus,
.react-fine-uploader-gallery-delete-button:focus,
.react-fine-uploader-gallery-pause-resume-button:focus,
.react-fine-uploader-gallery-retry-button:focus {
    outline: none;
}
.react-fine-uploader-gallery-cancel-button:focus svg,
.react-fine-uploader-gallery-delete-button:focus svg,
.react-fine-uploader-gallery-pause-resume-button:focus svg,
.react-fine-uploader-gallery-retry-button:focus svg {
    fill: grey;
}
.react-fine-uploader-gallery-cancel-button,
.react-fine-uploader-gallery-delete-button,
.react-fine-uploader-gallery-pause-resume-button,
.react-fine-uploader-gallery-retry-button {
    background: transparent;
    border: 0;
    position: absolute;
}
.react-fine-uploader-gallery-cancel-button,
.react-fine-uploader-gallery-delete-button {
    right: -18px;
    top: -12px;
}
.react-fine-uploader-gallery-pause-resume-button,
.react-fine-uploader-gallery-retry-button {
    left: -18px;
    top: -12px
}

.rh5v-DefaultPlayer_component {
    position: relative;
    font-family: Helvetica;
    font-size: 11px;
    background-color: #000;
}

.rh5v-DefaultPlayer_video {
    width: 100%;
    height: 100%;
}

.rh5v-DefaultPlayer_controls {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 34px;
    display: flex;
    background-color: rgba(0,0,0,0.7);
    opacity: 0;
    transition: opacity 0.2s;
}

.rh5v-DefaultPlayer_seek {
    flex-grow: 1;
}

.rh5v-DefaultPlayer_component:hover .rh5v-DefaultPlayer_controls {
    opacity: 1;
}
.rh5v-Time_component {
    padding: 0 10px 0 10px;
    line-height: 35px;
    color: #fff;
}

.rh5v-Time_current {
    margin-right: 5px;
}

.rh5v-Time_duration {
    margin-left: 5px;
    color: #919191;
}
.rh5v-Seek_component {
    position: relative;
}

.rh5v-Seek_track {
    position: absolute;
    top: 50%;
    left: 5px;
    right: 5px;
    height: 4px;
    transform: translateY(-50%);
    background-color: #3e3e3e;
}

.rh5v-Seek_buffer,
.rh5v-Seek_fill,
.rh5v-Seek_input {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
}

.rh5v-Seek_buffer {
    background-color: #5a5a5a;
}

.rh5v-Seek_fill {
    background: #fff;
}

.rh5v-Seek_input {
    width: 100%;
    opacity: 0;
    cursor: pointer;
}
.rh5v-Volume_component {
    position: relative;
}

.rh5v-Volume_component:hover {
    background-color: #000;
}

.rh5v-Volume_button {
    width: 34px;
    height: 34px;
    background: none;
    border: 0;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    cursor: pointer;
}

.rh5v-Volume_button:focus {
    outline: 0;
}

.rh5v-Volume_icon {
    padding: 7px;
}

.rh5v-Volume_slider {
    display: none;
    position: absolute;
    right: 5px;
    bottom: 100%;
    left: 5px;
    height: 56px;
    background-color: #000;
}

.rh5v-Volume_component:hover .rh5v-Volume_slider {
    display: block;
}

.rh5v-Volume_track {
    position: absolute;
    top: 8px;
    bottom: 8px;
    left: 50%;
    width: 4px;
    transform: translateX(-50%);
    background-color: #3e3e3e;
}

.rh5v-Volume_fill,
.rh5v-Volume_input {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.rh5v-Volume_fill {
    background-color: #fff;
}

.rh5v-Volume_input {
    padding: 0;
    margin: 0;
    opacity: 0;
    -webkit-appearance: slider-vertical;
    cursor: pointer;
}.rh5v-Captions_component {
    position: relative;
}

.rh5v-Captions_component:hover {
    background-color: #000;
}

.rh5v-Captions_button {
    width: 34px;
    height: 34px;
    background: none;
    border: 0;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    cursor: pointer;
}

.rh5v-Captions_button:focus {
    outline: 0;
}

.rh5v-Captions_icon {
    padding: 5px;
}

.rh5v-Captions_trackList {
    position: absolute;
    right: 0;
    bottom: 100%;
    display: none;
    background-color: rgba(0,0,0,0.7);
    list-style: none;
    padding: 0;
    margin: 0;
    color: #fff;
}

.rh5v-Captions_component:hover .rh5v-Captions_trackList {
    display: block;
}

.rh5v-Captions_trackItem {
    padding: 7px;
    cursor: pointer;
}

.rh5v-Captions_activeTrackItem,
.rh5v-Captions_trackItem:hover {
    background: #000;
}

.rh5v-Captions_activeTrackItem {
    text-decoration: underline;
}
.rh5v-PlayPause_component {}

.rh5v-PlayPause_component:hover {
    background-color: #000;
}

.rh5v-PlayPause_button {
    width: 34px;
    height: 34px;
    background: none;
    border: 0;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    cursor: pointer;
}

.rh5v-PlayPause_button:focus {
    outline: 0;
}

.rh5v-PlayPause_icon {
    padding: 5px;
}
.rh5v-Fullscreen_component {}

.rh5v-Fullscreen_component:hover {
    background-color: #000;
}

.rh5v-Fullscreen_button {
    width: 34px;
    height: 34px;
    background: none;
    border: 0;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    cursor: pointer;
}

.rh5v-Fullscreen_button:focus {
    outline: 0;
}

.rh5v-Fullscreen_icon {
    padding: 5px;
}
.rh5v-Overlay_component {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    color: #fff;
    text-align: center;
    cursor: pointer;
    background-color: rgba(0,0,0,0);
}

.rh5v-Overlay_inner {
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    left: 50%;
    width: 60px;
    height: 60px;
    transform: translateY(-50%);
    margin-left: -30px;
    background-color: rgba(0,0,0,0.7);
    border-radius: 10px;
}

.rh5v-Overlay_icon {
    position: absolute;
    top: 50%;
    right: 0;
    left: 50%;
    margin-left: -20px;
    transform: translateY(-50%);
}

